import Home from './Components/Home/Home'
import { Navigate, Route, Routes } from 'react-router-dom'
import NotFound from './Components/NotFound/NotFound';
import Register from './Components/Register/Register';
import Navbar from './Components/Navbar/Navbar';
import Login from './Components/Login/Login';
import { useCallback, useContext, useEffect } from 'react';
import { CurrentUserContext } from './Context/CurrentUserContext';
import AdminPanel from './Components/AdminPanel/AdminPanel';
import CreatePost from './Components/CreatePost/CreatePost';
import MyPosts from './Components/MyPosts/MyPosts';
import EditPost from './Components/EditPost/EditPost';
import EditUser from './Components/EditUser/EditUser';

function ForbiddenIfNotLoggedIn(props) {

  if (localStorage.getItem('token') === null) {
    return <Navigate to="/login" />
  } else {
    return props.children;
  }
}


function App() {

  const { decodeToken, currentUser } = useContext(CurrentUserContext);

  function ForbiddenIfNotAdmin(props) {
    if (currentUser?.role === 'user') {
      return <Navigate to="/home" />
    } else if (currentUser?.role === 'admin') {
      return props.children;
    }
  }

  const isLoggedIn = useCallback(
    () => {
      if (localStorage.getItem('token')) {
        decodeToken();
      }
    }, []);

  useEffect(() => {
    isLoggedIn();
  }, [isLoggedIn]);

  return <>

    <Navbar />

    <Routes>
      <Route path='/' element={<Navigate to='/home' />} />
      <Route path='/home' element={<ForbiddenIfNotLoggedIn><Home /></ForbiddenIfNotLoggedIn>} />
      <Route path='/create-post' element={<ForbiddenIfNotLoggedIn><CreatePost /></ForbiddenIfNotLoggedIn>} />
      <Route path='/my-posts' element={<ForbiddenIfNotLoggedIn><MyPosts /></ForbiddenIfNotLoggedIn>} />
      <Route path='/edit-post/:id' element={<ForbiddenIfNotLoggedIn><EditPost /></ForbiddenIfNotLoggedIn>} />
      <Route path='/edit-user/:id' element={
        <ForbiddenIfNotLoggedIn>
          <ForbiddenIfNotAdmin>
            <EditUser />
          </ForbiddenIfNotAdmin>
        </ForbiddenIfNotLoggedIn>
      } />
      <Route path='/admin' element={
        <ForbiddenIfNotLoggedIn>
          <ForbiddenIfNotAdmin>
            <AdminPanel />
          </ForbiddenIfNotAdmin>
        </ForbiddenIfNotLoggedIn>
      } />
      <Route path='/register' element={<Register />} />
      <Route path='/login' element={<Login />} />
      <Route path='*' element={<NotFound />} />
    </Routes>
  </>
}

export default App;
